<template>
<div class=" flex flex-row z-10 h-full w-full overflow-y-auto ">
    <router-view></router-view>
    <div class="flex sticky top-0 flex-col mb-14 px-4 ">
        <div class="flex flex-row py-1 ">
            <div class="flex flex-row cursor-pointer" @click="showCat_=true">
                <span class=" text-white text-xl">Categorie</span>
                <div class="ml-3"></div>
                <span v-if="selectedCat_==null" class=" text-green-500 text-xl ">Tout</span>
                <span class=" text-green-500 text-xl uppercase" v-if="index[1]!=null" v-text="selectedCat_==null?'/ '+index[1]:''+selectedCat_+'/'+index[1]"></span>
            </div>
            <div v-if="showCat_" class=" z-30 bg-gray-400 px-4  absolute ml-24 border-gray-700 border-b-4 rounded-lg shadow-xl">
                <div v-for="items,i in Object.keys(this.list_cat)" :key="i" @click="setCat_(items)" class="flex flex-col cursor-pointer text-sm hover:text-green-800 relative transform hover:scale-125 ">
                    <span v-text="items"></span>
                </div>
            </div>
        </div>
        <div v-if="showCat_">
            <div @click="showCat_=false" class=" flex w-full h-full top-0 left-0 z-20 absolute"> </div>
        </div>
        <div v-if="selectedCat_==null" class="flex flex-col overflow-y-auto pr-4 w-52">
            <div v-for="items,i in list_cat" :key="i" class="flex flex-col  text-gray-400">
                <div v-for="ite,k in items" :key="k" :title="items" class=" border-b uppercase text-xs mt-1 py-1 border-green-900  hover:border-gray-400 cursor-pointer" :class="index[1]==ite && index[0]==k?'rounded-full px-2 bg-green-500 text-gray-900':''" @click="setItem_(ite,k)">
                    <span v-text="verif_(ite)"></span>
                </div>
            </div>
        </div>
        <div v-else class="flex flex-col overflow-y-auto pr-4 w-52" style="max-height: 89vh">
            <div v-for="items,i in this.list_cat[this.selectedCat_]" :key="i" :title="items" class="flex flex-col  text-gray-400 border-b uppercase text-xs mt-1 py-1 border-green-900  hover:border-gray-400 cursor-pointer" :class="index[1]==items &&  index[0]==i?'rounded-full px-2 bg-green-500 text-gray-900':''" @click="setItem_(items,i)">
                <span v-text="verif_(items)"></span>
            </div>
        </div>
    </div>

    <div class="flex flex-col w-full">
        <div class="w-full" v-for="item,i in Object.keys(this.list_cat)" :key="i">
            <div class="flex w-full flex-col pr-4" v-if="item!='Tout'">
                <div class="border-b flex flex-row sticky top-0 z-10 bg-neutral-900 justify-between  border-b-green-800">
                    <div class="">
                        <span class=" text-xl text-green-500 font-semibold" v-text="item"></span>
                    </div>
                    <div>
                        <button @click="test()" class=" bg-img-grad text-white px-4"> Voire Plus</button>
                    </div>
                </div>
                <product />
            </div>
        </div>
    </div>

</div>
</template>

<script>
import product from "./accueil/gridProduct_.vue";
export default {
    components: {
        product
    },
    data() {
        return {
            showCat_: false,
            selectedCat_: null,
            index: [null, null],
            list_cat: {
                Tout: '',
                informatique: ['clavier', 'souris', 'usb', 'batterie', 'UC', 'disque dure', 'Carte mère', 'clavier gamer', 'boite d\' alimentation', 'cable VGA', 'Cable DVI'],
                electronique: ['lampe', 'Speaker', 'Casque', 'ecouteur', 'haut parleur', 'ecran TV', 'bafle', 'telephone', 'chargeur', 'cable'],
                quincalierie: ['Tole', 'PVC', 'Siment', 'Carreaux', 'Peinture'],
                cuisine: ['Assiette', 'cuillère', 'louche', 'marmite', 'cuvette', 'fourchette', 'seau', 'rice cooker', 'boulloir'],
                decoration: ['rideau', 'nape de table', 'couvre lit', 'couvre chèse']
            },
        }
    },
    methods: {
        setItem_(item, i) {
            this.index[0] = i;
            this.index[1] = item;
        },
        verif_(str) {
            if (str.length > 15)
                return str.substring(0, 15) + '...';
            return str;
        },
        setCat_(str) {
            if (str != 'Tout') {
                this.index[1] = str;
                this.selectedCat_ = str;
            } else {
                this.selectedCat_ = null
                this.index[1] = null
            }
            this.showCat_ = false;
        },
        test(){
            // console.log(URL.createObjectURL('dany'))
        }
    },
    mounted() {
    }, 
}
</script>
