<script>
export default {
    data() {
        return {
            isTop_: false,
            myDom:'',
            boite: this.$store.state.listeFourn_[this.$store.state.idFourn],
            categorie: [{
                id: 1,
                label: 'Informatique',
            }, {
                id: 2,
                label: 'Mode',
            }, {
                id: 3,
                label: 'Cuisine',
            }, ],
        }
    },
    methods: {
        checkScrolling_() {
            document.getElementById('fournFrame_').scrollTop * 100 / window.innerHeight >= 36 ? this.isTop_ = true : this.isTop_ = false;
              
            this.$store.state.scrollVal.boite.entrepriseBoite=document.getElementById('fournFrame_').scrollTop 
        },
        setRoute(item) {
            this.reinitiIdFourn()
            this.$router.replace({
                name: 'categorie',
                query: {
                    name: item.label + '_' + this.$store.state.idFourn
                },
                params: {
                    id: item.id
                }
            })
        },
        reinitiIdFourn() {
            var url = this.$route.query.name
            this.$store.state.idFourn == null ? this.$store.state.idFourn = url.split('_')[1] : '' 
        },
    },
    mounted() {   
        document.getElementById('fournFrame_').scrollTo(0, this.$store.state.scrollVal.boite.entrepriseBoite)
    },
    created() { 
        
        this.reinitiIdFourn() 
        this.boite= this.$store.state.listeFourn_[this.$store.state.idFourn]
    }
}
</script>

