import { createRouter, createWebHistory } from "vue-router";
import accueil from "../views/accueil_.vue";
import abonnee from "../views/abonnee_.vue";
import boite from "../views/boite_.vue";
import fournisseur from "../views/fournisseur/detail_";
import categorie from "../views/fournisseur/categorie_";
import description_ from "../views/fournisseur/descriptionOf_.vue";
import produit from "../views/accueil/produit_.vue"

const routes = [{
        path: "/accueil",
        name: "accueil",
        component: accueil,
        children: [{
            path: "/boiteProduct/:id",
            name: "produit",
            component: produit,
        }, ],
    },
    {
        path: "/fournisseur",
        name: "fournisseur",
        component: fournisseur,
        children: [{
            path: "/boite/:id",
            name: "categorie",
            component: categorie,
        }, ],
    },
    {
        path: "/abonnee",
        name: "abonnee",
        component: abonnee,
    },

    {
        path: "/produit/:id",
        name: "detailProd",
        component: description_,
    },
    {
        path: "/boite",
        name: "boite",
        component: boite,
        // children: [{
        //     path: "/boite/:id",
        //     name: "detailProd",
        //     component: description_,
        // }, ],
    },
    {
        path: "",
        redirect: "/accueil",
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
});
export default router;